.contact {
  position: absolute;
  width: 320px;
  margin-left: -160px;
  left: 50%;
  top: 30%;
  background: transparent;
}

.contact__title {
  text-align: center;
  margin-bottom: 20px;
}

.contact__form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.contact__title-box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.contact__submit,
.contact__input {
  background-color: rgb(215, 215, 215);
  border: none;
  padding: 10px;
  margin: 10px;
  -ms-flex-positive: 1;
      flex-grow: 1;
  min-width: 300px;
}

.contact__submit {
  background-color: rgb(147, 160, 244);
  -webkit-box-shadow: none;
          box-shadow: none;
  color: #FFF;
  -webkit-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
  cursor: pointer;
}

.contact__submit:hover {
  background-color: rgb(87, 101, 194);
  -webkit-box-shadow: 3px 6px 0px #9AA3A8;
          box-shadow: 3px 6px 0px #9AA3A8;
}



